import { Injectable } from '@angular/core';

import { Storage } from "@ionic/storage";

@Injectable()
export class ConfigProvider {

	private _url: string = 'http://www.mor2plg.id/buset/';
	public _contentType: string = 'application/x-www-form-urlencoded';

	constructor(private storage: Storage) {
		this.storage.set('CONF_URL', this._url);
	}

	setCustId(custId)
	{
		this.storage.set('CONFIG_CUSTID', custId);
	}

	getCustId():Promise<any>
	{
		return this.storage.ready().then(()=>this.storage.get('CONFIG_CUSTID'));
	}

	setLevel(level:string)
	{
		this.storage.set('CONF_LEVEL', level);
	}

	getLevel(): Promise<any>
	{
		return this.storage.ready().then(()=>this.storage.get('CONF_LEVEL'));
	}

	setToken(bearer:string, token:string)
	{
		this.storage.set('CONF_TOKEN', bearer+' '+token);
	}

	getToken():Promise<any>
	{
		return this.storage.ready().then(() => this.storage.get('CONF_TOKEN'));
	}

	setExpToken(expToken:number)
	{
		this.storage.set('CONF_EXPTOKEN', expToken);
	}

	getExpToken():Promise<any>
	{
		return this.storage.ready().then(()=>this.storage.get('CONF_EXPTOKEN'));
	}
	
	setUrl( url = '' )
	{
		this.storage.set('CONF_URL', url); 
	}

	getUrl():Promise<any>
	{
		return this.storage.ready().then(()=>this.storage.get('CONF_URL'));
	}

	setLogin(loggedIn)
	{
		this.storage.set('CONF_LOGIN', loggedIn);
	}

	checkLogin():Promise<any>
	{
		return this.storage.ready().then(()=>this.storage.get('CONF_LOGIN'));
	}

	setProfile(pict:string)
	{
		this.storage.set('CONF_PROFILE', pict);
	}

	getProfile():Promise<any>
	{
		return this.storage.ready().then(()=>this.storage.get('CONF_PROFILE'));
	}

	setIdLogin(id: string)
	{
		this.storage.set('CONF_IDLOGIN', id);
	}

	getIdLogin():Promise<any>
	{
		return this.storage.ready().then(() => this.storage.get('CONF_IDLOGIN'));
	}

	encodedObject(Data:any)
	{
		return Object.keys(Data).map(function (key) { 
			return encodeURIComponent(key) + '=' + encodeURIComponent(Data[key]);
		}).join('&');
	}

	delToken()
	{
		this.storage.remove('CONF_TOKEN');
	}

	delExpToken()
	{
		this.storage.remove('CONF_EXPTOKEN');
	}

	delLogedIn()
	{
		this.storage.remove('CONF_LOGIN');
	}

	delProfile()
	{
		this.storage.remove('CONF_PROFILE');
	}
}



