import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { ConfigProvider } from '../../providers/config/config';
import { CommonProvider } from '../../providers/common/common';
import { LoginProvider } from '../../providers/login/login';
import { SettingProvider } from '../../providers/setting/setting';
import { Camera, CameraOptions } from '@ionic-native/camera';


@IonicPage()
@Component({
	selector: 'page-setting',
	templateUrl: 'setting.html',
})
export class SettingPage {

	private Profile: any;
	private FormProfile: FormGroup;
	private _confEmail: string;
	private PhotoDisplay: string;

	private _Id: AbstractControl;
	private _CreatedDate: AbstractControl;
	private _UpdatedDate: AbstractControl;
	private _StatusLogin: AbstractControl;
	private _CustomerId: AbstractControl;
	private _ApprovedBy: AbstractControl;
	private _NoCustomer: AbstractControl;
	private _Email: AbstractControl;
	private _Password: AbstractControl;
	private _Level: AbstractControl;
	private _ContactPerson: AbstractControl;
	private _ContactPerson1: AbstractControl;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		private formBuilder: FormBuilder,
		private config: ConfigProvider,
		private common: CommonProvider,
		private loginService: LoginProvider,
		private camera: Camera,
		private setting: SettingProvider
	) {
		this.config.getProfile().then((res:string) => { 
			this._confEmail = res;
		});
		
		this.FormProfile = this.formBuilder.group({
			Id: ['', Validators.required],
			CreatedDate: ['', Validators.required],
			UpdatedDate: [''],
			StatusLogin: [''],
			CustomerId: ['', Validators.required],
			ApprovedBy: [''],
			NoCustomer: ['', Validators.required],
			Email: ['', Validators.compose([Validators.maxLength(70), Validators.pattern('^[_A-Za-z0-9-\\+]+(\\.[_A-Za-z0-9-]+)*@[A-Za-z0-9-]+(\\.[A-Za-z0-9]+)*(\\.[A-Za-z]{2,})$'), Validators.required])],
			Password: ['', Validators.compose([Validators.minLength(3), Validators.maxLength(12)])],
			Level: ['', Validators.required],
			ContactPerson: ['', Validators.required], 
			ContactPerson1: ['']	
		});
		this._Id = this.FormProfile.controls['Id'];
		this._CreatedDate = this.FormProfile.controls['CreatedDate'];
		this._UpdatedDate = this.FormProfile.controls['UpdatedDate']; 
		this._StatusLogin = this.FormProfile.controls['StatusLogin'];
		this._CustomerId = this.FormProfile.controls['CustomerId'];
		this._ApprovedBy = this.FormProfile.controls['ApprovedBy'];
		this._NoCustomer = this.FormProfile.controls['NoCustomer'];
		this._Email = this.FormProfile.controls['Email'];
		this._Password = this.FormProfile.controls['Password'];
		this._Level = this.FormProfile.controls['Level'];
		this._ContactPerson = this.FormProfile.controls['ContactPerson'];
		this._ContactPerson1 = this.FormProfile.controls['ContactPerson1'];
	}

	ionViewDidLoad() {
		this.common.showLoading();
		setTimeout(() => {
			this.common.stopLoading();
			this.loadData();
		}, 3000);
	}

	loadData()
	{
		console.log(this._confEmail);
		this.common.showLoading();
		this.loginService.getProfile(this._confEmail) // get profile and save to storage local
			.then((res: any) => { 
			
				this.Profile = {
					Id: res.id,
					CreatedDate: res.createdDate,
					UpdatedDate: res.updatedDate,
					StatusLogin: res.statusLogin,
					CustomerId: res.customerId,
					ApprovedBy: res.approvedBy,
					NoCustomer: res.noCustomer,
					Email: res.email,
					Password: res.password,
					Level: res.level,
					ContactPerson: res.contactPerson,
					ContactPerson1: res.contactPerson1
				};

				this.FormProfile.setValue(this.Profile);
				this.PhotoDisplay = res.photo;	
				this.common.stopLoading();
			
		}).catch((err: any) => { 
			console.log(err);
			this.common.stopLoading();
			this.common.presentToast("Error : Cannot load data...");
		});
		
		
	}

	getPicture()
	{ 
		const options: CameraOptions = {
			quality: 20,
			destinationType: this.camera.DestinationType.DATA_URL,
			encodingType: this.camera.EncodingType.JPEG,
			mediaType: this.camera.MediaType.PICTURE
		};
		
		this.camera.getPicture(options).then((img) => {
			this.PhotoDisplay = 'data:image/png;base64,'+img;
		}, (err) => {
			console.log(err);
			this.common.presentToast("Error: " + JSON.stringify(err));
		});
	}

	simpan()
	{
		this.common.showLoading();
		
		this.Profile = {
			Id: this.FormProfile.value.Id,
			Email: this.FormProfile.value.Email,
			Password: this.FormProfile.value.Password,
			Level: this.FormProfile.value.Level,
			CreatedDate: this.FormProfile.value.CreatedDate,
			UpdatedDate: new Date().toISOString(),
			StatusLogin: this.FormProfile.value.StatusLogin,
			CustomerId: this.FormProfile.value.CustomerId,
			ApprovedBy: this.FormProfile.value.ApprovedBy,
			ContactPerson: this.FormProfile.value.ContactPerson,
			ContactPerson1: this.FormProfile.value.ContactPerson1,
			NoCustomer: this.FormProfile.value.NoCustomer
		};

		this.setting.saveProfile(this.Profile, this.PhotoDisplay).then((res) => { 

			this.common.presentAlert('Sukses',"Sukses menyimpan data...");
			this.common.stopLoading();
		}, (err) => { 
			console.log(err);
			this.common.presentAlert('Error', 'Gagal menyimpan data');
			this.common.stopLoading();
		});
		
	}

}



