import { Http, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import { ConfigProvider } from '../config/config';

@Injectable()
export class SettingProvider {

	private _token: string;
	private _url: string;

	constructor(
		public http: Http,
		private config: ConfigProvider
	) {
		this.config.getToken().then((token) => { 
			this._token = token;
		});
		this.config.getUrl().then((url) => { 
			this._url = url;
		});
	}

	saveProfile(Profile:any, DisplayPhoto: string)
	{
		if (DisplayPhoto != null)
		{
			Profile.Photo = DisplayPhoto;
		}	
		var headers = new Headers();
		headers.append('Content-Type', 'application/json');
		headers.append('Authorization', this._token);
		return new Promise((resolve, reject) => { 
			this.http.post(this._url + 'api/Profile/'+Profile.Id, Profile, { headers: headers })
				.subscribe((res) => { 
					resolve(res.json());
				}, (err) => {
					reject(err);
				});
		});
	}

}



