import { Component } from '@angular/core';
import { NavController, NavParams, AlertController } from 'ionic-angular';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { LoginModel } from '../../models/login';
import { ConfigProvider } from '../../providers/config/config';
import { CommonProvider } from '../../providers/common/common';
import { LoginProvider } from '../../providers/login/login';
import { UtamaPage } from '../utama/utama';
import { RegisterPage } from '../register/register';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

	private _url: string;
	// UI
	private FormLogin: FormGroup;
	private email: AbstractControl;
	private password: AbstractControl;
	private User: LoginModel;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public alertCtrl: AlertController,
		private config: ConfigProvider,
		private formsBuilder: FormBuilder,
		private common: CommonProvider,
		private loginService: LoginProvider
	) {
		// get Url
		this.config.getUrl().then(url => { 
			this._url = url;
		});

		this.FormLogin = this.formsBuilder.group({
			email:['', Validators.compose([Validators.maxLength(70), Validators.pattern('^[_A-Za-z0-9-\\+]+(\\.[_A-Za-z0-9-]+)*@[A-Za-z0-9-]+(\\.[A-Za-z0-9]+)*(\\.[A-Za-z]{2,})$'), Validators.required])],
			password:['', Validators.compose([Validators.maxLength(30), Validators.minLength(6), Validators.required])]
		});

		this.email = this.FormLogin.controls['email'];
		this.password = this.FormLogin.controls['password'];
	}
	
	// Login Action
	login():void
	{
		this.common.showLoading();
		if (!this.FormLogin.valid)
		{
			this.common.presentAlert('Konfirmasi', 'Form Login Required');
			this.common.stopLoading();
		}
		else
		{
			this.User = {
				UserName: this.FormLogin.value.email,
				Password: this.FormLogin.value.password,
				grant_type: 'password'
			};
			this.common.presentToast('Authentication...');
			this.loginService.authLogin(this.User).then((result: any) => { // login proses 
				this.config.setLogin(true);
				this.config.setToken(result.token_type, result.access_token); //save token to localstorage
				this.config.setExpToken(result.expires_in); // save expired token
				this.config.setProfile(result.UserName); 
				this.common.presentToast('Load Profile..');
				this.loginService.getProfile(result.UserName) // set level to local storage
					.then((prof: any) => {
						
						this.config.setLevel(prof.level); // set level
						this.common.presentToast('Load Level..');
						this.loginService.getIdLogin(result.UserName) //set id to local storage
						.then((resId: string) => { 
							this.common.presentToast('Redirect to Home Page..');
							this.config.setIdLogin(resId);
							this.common.stopLoading();
							this.navCtrl.setRoot(UtamaPage);
							
						}, (err) => { 
							this.common.stopLoading();
							console.log(err);
							//this.common.presentToast(JSON.stringify(err));
						});

					}, (err) => {
						this.common.stopLoading();
						console.log(err);
					});
				
			}).catch((err: any) => { 
				this.common.stopLoading();
				this.common.presentToast("Email atau Password anda salah. silahlan coba lagi..");
			});
			
		}
		
	}

	

	// Set Hostname Action
  	setHostname()
  	{
		let alert = this.alertCtrl.create({
			title: 'Set Hostname',
			inputs: [
				{
					name: 'hostname',
					placeholder: this._url
				}
			],
			buttons: [
				{
					text: 'Batal',
					handler: data => { 
						console.log('Terminate!');
					}
				},
				{
					text: 'Simpan',
					handler: data => { 
						var fHost = data.hostname;
						if (fHost.length > 0)
						{
							this.config.setUrl(fHost);
							this._url = fHost;
						}
						else
						{
							this.config.setUrl(this._url);
						}	
					}
				}
			]
		});
		alert.present();	
	}
	
	setRegister():void
	{
		this.navCtrl.setRoot(RegisterPage);
	}

}



