import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { CommonProvider } from '../../providers/common/common';
import { ConfigProvider } from '../../providers/config/config';
import { LoginProvider } from '../../providers/login/login';
import { UploadProvider } from '../../providers/upload/upload';


@IonicPage()
@Component({
	selector: 'page-upload',
	templateUrl: 'upload.html',
})
export class UploadPage {

	private _id: string;
	private _email: string;

	private FormBukti: FormGroup;
	private JumlahSetor: AbstractControl;
	private Sosa: AbstractControl;
	private Keterangan: AbstractControl;

	private User: any;

	private ImageSave: any;
	private Bukti: any;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		private common: CommonProvider,
		private config: ConfigProvider,
		private profile: LoginProvider,
		private formBuilder: FormBuilder,
		private camera: Camera,
		private upload: UploadProvider
	) {

		this.config.getIdLogin().then(id => {
			this._id = id;
		});
		this.config.getProfile().then(email => {
			this._email = email;
		});

		this.FormBukti = this.formBuilder.group({
			JumlahSetor: ['', Validators.compose([Validators.pattern('[0-9]'), Validators.required])],
			Sosa: [''],
			Keterangan: ['']
		});

		this.JumlahSetor = this.FormBukti.controls['JumlahSetor'].value;
		this.Sosa = this.FormBukti.controls['Sosa'].value;
		this.Keterangan = this.FormBukti.controls['Keterangan'].value;
	}

	ionViewDidLoad() {
		this.common.showLoading();
		setTimeout(() => {
			this.common.stopLoading();
			this.getProfile();
		}, 3000);
	}

	getProfile()
	{
		this.common.showLoading();
		this.profile.getProfile(this._email)
			.then((res) => {
				this.User = res;
				console.log(res);
				this.common.stopLoading();
			}, (err) => {
				console.log(err);
				this.common.presentAlert('Error', 'Terjadi kegagalan saat mengambil data...');
				this.common.stopLoading();
			});
	}

	simpan()
	{
		this.common.showLoading();
		if (!this.ImageSave)
		{
			this.common.presentAlert('Error', 'Silahkan ambil gambar bukti...');
			this.common.stopLoading();
		}	
		else
		{
			var tgl = new Date();
			this.Bukti = {
				Vendor: this._email,
				TanggalSetor: tgl.toISOString(),
				JumlahSetor: this.FormBukti.controls['JumlahSetor'].value,
				Keterangan: this.FormBukti.controls['Keterangan'].value,
				Attachment: this.ImageSave,
				LoginId: parseInt(this._id),
				StatusBaca: 0,
				Sosa: this.FormBukti.controls['Sosa'].value,
				ApprovedByWho: "0"
			};
			console.log(this.Bukti);
			this.upload.save(this.Bukti).then((res) => {
				this.common.presentAlert('Sukses', 'Sukses menimpan data...');
				this.common.stopLoading();
				
				var BuktiEmpty = {
					JumlahSetor: "",
					Keterangan: "",
					Sosa: "",
				};
				this.FormBukti.setValue(BuktiEmpty);

			}, (err) => {
				this.common.presentAlert('Error', 'Gagal menyimpan data..');
				console.log(err);
				this.common.stopLoading();
			});

			
		}	
	}

	capture()
	{
		var camOptions: CameraOptions = {
			quality: 20,
			destinationType: this.camera.DestinationType.DATA_URL,
			encodingType: this.camera.EncodingType.JPEG,
			mediaType: this.camera.MediaType.PICTURE
		};
		this.camera.getPicture(camOptions)
			.then((img: any) => {
				this.ImageSave = 'data:image/png;base64,'+img;
			}, (err) => { 
				console.log(err);
				this.common.presentAlert('Error', 'Error while get picture...');
			});
	}

}



