import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, PopoverController } from 'ionic-angular';
import { FormGroup, AbstractControl, FormBuilder, Validators } from '@angular/forms';
import { CommonProvider } from '../../providers/common/common';
import { LoginProvider } from '../../providers/login/login';
import { HomePage } from '../home/home';
import { CustomerSearchPage } from '../customer-search/customer-search';
import { Camera, CameraOptions } from '@ionic-native/camera';

@IonicPage()
@Component({
	selector: 'page-register',
	templateUrl: 'register.html',
})
export class RegisterPage {

	private FormRegister: FormGroup;
	private email: AbstractControl;
	private password: AbstractControl;
	private level: AbstractControl;
	private customerid: AbstractControl;
	private contactperson: AbstractControl;
	private contactperson1: AbstractControl;
	private PhotoDisplay: string = null;
	private customer: string;
	private _customerId: string;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		private formBuilder: FormBuilder,
		private camera: Camera,
		private prop: PopoverController,
		private common: CommonProvider,
		private reg: LoginProvider
	) {

		this.FormRegister = this.formBuilder.group({
			email: ['',  Validators.compose([Validators.maxLength(70), Validators.pattern('^[_A-Za-z0-9-\\+]+(\\.[_A-Za-z0-9-]+)*@[A-Za-z0-9-]+(\\.[A-Za-z0-9]+)*(\\.[A-Za-z]{2,})$'), Validators.required])],
			password: ['', Validators.compose([Validators.maxLength(30), Validators.minLength(6), Validators.required])],
			level: ['', Validators.required],
			customerid: ['', Validators.required],
			contactperson: ['', Validators.required],
			contactperson1: ['']
		});

		this.email = this.FormRegister.controls['email'];
		this.password = this.FormRegister.controls['password'];
		this.level = this.FormRegister.controls['level'];
		this.customerid = this.FormRegister.controls['customerid'];
		this.contactperson = this.FormRegister.controls['contactperson'];
		this.contactperson1 = this.FormRegister.controls['contactperson1'];
		
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad RegisterPage');
	}

	browse()
	{
		const options: CameraOptions = {
			quality: 20,
			destinationType: this.camera.DestinationType.DATA_URL,
			encodingType: this.camera.EncodingType.JPEG,
			mediaType: this.camera.MediaType.PICTURE
		};
		
		this.camera.getPicture(options).then((img) => {
			this.PhotoDisplay = 'data:image/png;base64,'+img;
		}, (err) => {
			console.log(err);
		});
		console.log(this.PhotoDisplay);
	}

	searching()
	{
		var propover = this.prop.create(CustomerSearchPage, {}, {
			cssClass: 'proper-customer'
		});
		propover.onDidDismiss((data: any) =>
		{
			if (data.hasOwnProperty('nama'))
			{
				this.customer = data.nama;
				this._customerId = data.id;	
			}
		});
		propover.present();

	}

	simpan()
	{
		
		var UserReg = {
			Email: this.FormRegister.value.email,
			Password: this.FormRegister.value.password,
			Level: this.FormRegister.value.level,
			CreatedDate: new Date().toISOString(),
			UpdatedDate: new Date().toISOString(),
			StatusLogin: 0,
			CustomerId: this._customerId,
			ApprovedBy: null,
			ContactPerson: this.FormRegister.value.contactperson,
			ContactPerson1: this.FormRegister.value.contactperson1,
			NoCustomer: '0000',
			Photo: this.PhotoDisplay ? this.PhotoDisplay : null
		};

		console.log(UserReg);

		this.common.showLoading();
		this.reg.daftarUserBaru(UserReg)
			.then((res) => {
				this.common.stopLoading();
				this.common.presentAlert('Sukses', 'Sukses mendaftarkan data baru');
				this.navCtrl.setRoot(HomePage);
			}).catch((err) => {
				this.common.stopLoading();
				this.common.presentToast('Gagal mendaftarkan user baru. Silahkan Coba lagi');
			});
		

	}

	setLogin():void
	{
		this.navCtrl.setRoot(HomePage);
	}
}



