import { Component, ElementRef, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { CommonProvider } from '../../providers/common/common';
import { LoginProvider } from '../../providers/login/login';

@IonicPage()
@Component({
	selector: 'page-customer-search',
	templateUrl: 'customer-search.html',
})
export class CustomerSearchPage {

	@ViewChild('search_input') searchInput: ElementRef

	private _resultIf: Boolean;
	private _search: string;
	private _listSearch: any;
	private _loading: Boolean;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		private common: CommonProvider,
		private reg: LoginProvider,
		private viewCtrl: ViewController
	) {
		this._resultIf = false;
	}

	ionViewDidLoad() {
		console.log('ionViewDidLoad CustomerSearchPage');
	}

	appendItem(id=0, nama='')
	{
		this.viewCtrl.dismiss({id: id, nama: nama});
	}

	
	onInput(ev)
	{
		this._loading = true;
		console.log(this._search);
		this._resultIf = true;
		this.reg.searchCustomer(this._search)
			.then((res:string) => {
				this._listSearch = JSON.parse(res);
				this._loading = false;

			}).catch((err) => {
				this.common.presentAlert('Error', 'Data tidak ada..');
				this._loading = false;
			});
		this.focus();
	}

	onCancel(ev)
	{
		this._loading = true;
		this._resultIf = false;
		this._loading = false;
	}

	private focus()
	{
		if (this.searchInput && this.searchInput.nativeElement)
		{
			this.searchInput.nativeElement.focus();
		}	
	}

}



