import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { CommonProvider } from '../../providers/common/common';
import { HistoryProvider } from '../../providers/history/history';


@IonicPage()
@Component({
	selector: 'page-detail-bukti',
	templateUrl: 'detail-bukti.html',
})
export class DetailBuktiPage {

	private _id: number;
	private Attachment: string;
	private Vendor: string;
	private JumlahSetor: string;
	private TanggalSetor: Date;
	private Sosa: string;
	private ApprovedBy: string;
	private Keterangan: string;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		private common: CommonProvider,
		private history: HistoryProvider
	) {
		this._id=this.navParams.get('id');
	}

	ionViewDidLoad() {
		this.loadData();
	}

	loadData()
	{ 
		this.common.showLoading();
		this.history.getDetailBukti(this._id)
			.then((res:any) => { 
				var detail = JSON.parse(res);
				this.Attachment = detail.Attachment;
				this.Vendor = detail.Vendor;
				this.JumlahSetor = detail.JumlahSetor;
				this.Sosa = detail.Sosa;
				this.TanggalSetor = detail.TanggalSetor;
				this.ApprovedBy = detail.ApprovedBy;
				this.Keterangan = detail.Keterangan;
				this.common.stopLoading();
			}, (err) => {
				console.log(err);
				this.common.presentAlert('Error', 'Terjadi kesalahan saat mengambil data');
				this.common.stopLoading();
			});
	}

}



