import { Http, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/scan';
import 'rxjs/add/operator/delay';
import 'rxjs/add/operator/retryWhen';
import 'rxjs/add/operator/retry';
import 'rxjs/add/operator/finally';
import 'rxjs/add/operator/delayWhen';
import 'rxjs/add/operator/timeout';
import 'rxjs/add/observable/throw';
import 'rxjs/add/observable/onErrorResumeNext';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/catch';

import { ConfigProvider } from "../config/config";


@Injectable()
export class LoginProvider {

	private _url: string;
	private _token: string;

	constructor(
		public http: Http,
		private config: ConfigProvider
	) { 
		this.config.getUrl().then((url) => { 
			this._url = url;
		});

		this.config.getToken().then((token) => { 
			this._token = token;
		});
		
	}

	authLogin(User:any)
	{
		return new Promise((resolve, reject) => { 
			var headers = new Headers();
			headers.append('Content-Type', this.config._contentType);
			headers.append('Access-Control-Allow-Origin', '*');
			this.http.post(this._url + 'token', this.config.encodedObject(User), { headers: headers })
				.retry(3)	
				.subscribe((data:any) => {
					resolve(data.json());
				}, (err) => {
					reject(err);
				});
		});
	}

	getProfile(Email: string)
	{
		var headers = new Headers();
		headers.append('Content-Type', this.config._contentType);
		headers.append('Authorization', this._token); 
		headers.append('Access-Control-Allow-Origin', '*');
		return new Promise((resolve, reject) => { 
			this.http.get(this._url + 'api/Profile?Email='+Email , { headers: headers })
				.subscribe((res:any) => { 
					resolve(res.json());
				}, (err) => { 
					reject(err);
				});
		});
	}

	getIdLogin(Email:string)
	{
		var headers = new Headers();
		headers.append('Content-Type', this.config._contentType);
		headers.append('Authorization', this._token);
		headers.append('Access-Control-Allow-Origin', '*');
		return new Promise((resolve, reject) => {
			this.http.get(this._url + 'api/getIdLogin?Email=' + Email, { headers: headers })
				.subscribe((res) => { 
					resolve(res.json());
				}, (err) => {
					reject(err);
				});
		});
	}

	getPhotoByEmail(Email:string)
	{
		var headers = new Headers();
		headers.append('Content-Type', this.config._contentType);
		headers.append('Authorization', this._token);
		headers.append('Access-Control-Allow-Origin', '*');
		return new Promise((resolve, reject) => {
			this.http.get(this._url + 'api/getPhoto?Email=' + Email, { headers: headers })
				.subscribe((res) => {
					resolve(res.json());

				}, (err) => {
					reject(err);
				});
		});
	}

	searchCustomer(Nama: string)
	{
		return new Promise((resolve, reject) => {
			var headers = new Headers();
			headers.append('Content-Type', this.config._contentType);
			headers.append('Access-Control-Allow-Origin', '*');
			this.http.get(this._url + 'api/Customer?Nama=' + Nama, { headers: headers })
				.subscribe((res) => {
					resolve(res.json());
				}, (err) => {
					reject(err);
				});
		});
	}

	daftarUserBaru(User: any)
	{
		return new Promise((resolve, reject) => {
			var headers = new Headers();
			headers.append('Content-Type', this.config._contentType);
			headers.append('Access-Control-Allow-Origin', '*');
			this.http.post(this._url + 'api/Coba', this.config.encodedObject(User), { headers: headers })
				.subscribe((res) => {
					resolve(res);
				}, (err) => {
					reject(err);
				});
		});
	}


	logout()
	{
		this.config.delToken();
		this.config.delProfile();
		this.config.delLogedIn();
		this.config.delExpToken();
		this.config.setLogin(false);
	}

}



