import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { Camera } from "@ionic-native/camera";
import { EmailComposer } from "@ionic-native/email-composer";

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { RegisterPage } from '../pages/register/register';
import { UtamaPage } from '../pages/utama/utama';
import { HttpModule } from '@angular/http';
import { PipesModule } from '../pipes/pipes.module';
import { ComponentsModule } from '../components/components.module';
import { ConfigProvider } from '../providers/config/config';
import { CommonProvider } from '../providers/common/common';
import { BuktiModel } from '../models/bukti';
import { UserModel } from '../models/user';
import { CustomerModel } from '../models/customer';
import { LoginModel } from '../models/login';
import { LoginProvider } from '../providers/login/login';
import { SettingProvider } from '../providers/setting/setting';
import { HistoryProvider } from '../providers/history/history';
import { UploadProvider } from '../providers/upload/upload';
import { EmojiProvider } from '../providers/emoji/emoji';
import { IonicStorageModule } from '@ionic/storage';
import { CustomerSearchPage } from '../pages/customer-search/customer-search';
import { HistoryPage } from '../pages/history/history';
import { UploadPage } from '../pages/upload/upload';
import { ChatingPage } from '../pages/chating/chating';
import { SettingPage } from '../pages/setting/setting';
import { DetailBuktiPage } from '../pages/detail-bukti/detail-bukti';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    RegisterPage,
    UtamaPage,
    HistoryPage,
    UploadPage,
    ChatingPage,
    SettingPage,
    DetailBuktiPage,
    CustomerSearchPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    PipesModule,
    ComponentsModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    RegisterPage,
    UtamaPage,
    HistoryPage,
    UploadPage,
    ChatingPage,
    SettingPage,
    DetailBuktiPage,
    CustomerSearchPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    ConfigProvider,
    CommonProvider,
    BuktiModel,
    UserModel,
    CustomerModel,
    LoginModel,
    LoginProvider,
    SettingProvider,
    HistoryProvider,
    UploadProvider,
    EmojiProvider,
    Camera,
    EmailComposer
  ]
})
export class AppModule {}



