import { Pipe, PipeTransform } from '@angular/core';


@Pipe({
  	name: 'customdate',
})
export class CustomdatePipe implements PipeTransform {
  
	transform(value: Date, ...args) {
		return value.getDate() + '/' + value.getMonth() + '/' + value.getFullYear();
	}
}



