import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { LoginProvider } from '../../providers/login/login';
import { ConfigProvider } from '../../providers/config/config';
import { CommonProvider } from '../../providers/common/common';
import { HomePage } from '../home/home';
import { ChatingPage } from '../chating/chating';
import { SettingPage } from '../setting/setting';
import { HistoryPage } from '../history/history';
import { UploadPage } from '../upload/upload';
import { EmailComposer } from '@ionic-native/email-composer';

@IonicPage()
@Component({
  selector: 'page-utama',
  templateUrl: 'utama.html',
})
export class UtamaPage {

	private _photo: string;
	private _level: string;
	private _loadPhoto: Boolean;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public auth: LoginProvider,
		private config: ConfigProvider,
		private mail: EmailComposer,
		private common: CommonProvider
	) {
		this.config.getLevel().then((lvl) => {
			this._level = lvl;
		});
	}

	ionViewDidLoad() {
		
	}

	ionViewDidEnter() {
		this.getPhoto();
	}

	private getPhoto()
	{
		this._loadPhoto = true;
		this.config.getProfile()
			.then((email) => {
				this.auth.getPhotoByEmail(email)
					.then((photo: string) => { 
						this._photo = photo;
						this._loadPhoto = false;
					}, (err) => {
						console.log("Error: " + JSON.stringify(err));
						this._loadPhoto = false;
					});
			});
	}

	histori()
	{
		this.navCtrl.push(HistoryPage);
	}

	upload()
	{
		this.navCtrl.push(UploadPage);
	}

	email()
	{
		var emailOpt = {
			to: '',
			subject: '',
			body: '',
			isHtml: true
		};
		this.mail.isAvailable()
			.then((available: boolean) => {
				if (!available) {
					this.common.presentAlert('Error', 'Email tidak tersedia. mohon install gmail di android anda...');
				}
			});
		this.mail.open(emailOpt);
	}

	chat()
	{
		this.navCtrl.push(ChatingPage);
	}

	setting()
	{
		this.navCtrl.push(SettingPage);
	}

	logout() 
	{
		this.auth.logout();
		this.navCtrl.setRoot(HomePage);
	}

}



