import { Http, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/scan';
import 'rxjs/add/operator/delay';
import 'rxjs/add/operator/retryWhen';
import 'rxjs/add/operator/finally';
import 'rxjs/add/operator/delayWhen';
import 'rxjs/add/operator/timeout';
import 'rxjs/add/observable/throw';
import 'rxjs/add/observable/onErrorResumeNext';
import 'rxjs/add/operator/toPromise';

import { ConfigProvider } from '../config/config';

@Injectable()
export class HistoryProvider {

	private _url: string;
	private _token: string;
	private _idLogin: string;

	constructor(
		public http: Http,
		private config: ConfigProvider
	) {
		config.getUrl().then(url => { 
			this._url = url;
		});
		config.getToken().then(token => {
			this._token = token;
		});
		config.getIdLogin().then(idLogin => {
			this._idLogin = idLogin;
		});
	}

	getHistoryByLogin()
	{
		var headers = new Headers();
		headers.append('Content-Type', this.config._contentType);
		headers.append('Authorization', this._token);
		return new Promise((resolve, reject) => { 
			this.http.get(this._url + 'api/History/' + this._idLogin, { headers: headers })
				.subscribe(res => { 
					resolve(res.json());
				}, (err) => { 
					reject(err);
				});
		});
	}

	getDetailBukti(id)
	{
		var headers = new Headers();
		headers.append('Content-Type', this.config._contentType);
		headers.append('Authorization', this._token);
		return new Promise((resolve, reject) => {
			this.http.get(this._url + 'api/DetailHistory/' + id, { headers: headers })
				.subscribe((res:any) => {
					resolve(res.json());
				}, (err) => {
					reject(err);
				});
		});
	}

	getByCustomer(id)
	{
		var headers = new Headers();
		headers.append('Content-Type', this.config._contentType);
		headers.append('Authorization', this._token);
		return new Promise((resolve, reject) => {
			this.http.get(this._url + 'api/HistoryCustomer/' + id, { headers: headers })
				.subscribe((res: any) => {
					resolve(res.json());
				}, (err) => {
					reject(err);
				});
		});
	}

}



