import { Injectable } from "@angular/core";

@Injectable()
export class BuktiModel { 

    constructor() { }

    public Id?: number;
    public Vendor: string;
    public TanggalSetor: Date;
    public JumlahSetor: string;
    public Keterangan: string;
    public Attachment: string;
    public LoginId: number;
    public StatusBaca: boolean;
    public Sosa: string;
    public ApprovedByWho: string;

}


