import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { HistoryProvider } from '../../providers/history/history';
import { CommonProvider } from '../../providers/common/common';
import { ConfigProvider } from '../../providers/config/config';
import { DetailBuktiPage } from '../detail-bukti/detail-bukti';



@IonicPage()
@Component({
	selector: 'page-history',
	templateUrl: 'history.html',
})
export class HistoryPage {

	private _bukti: any;
	private _level: string;
	private _custId: string;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		private history: HistoryProvider,
		public common: CommonProvider,
		private config: ConfigProvider
	) {
		this.config.getLevel()
			.then((level) => {
				this._level = level;
			});
		this.config.getCustId().then((custId) => {
			this._custId = custId;
		});
	}

	ionViewDidLoad() {
		this.common.showLoading();
		setTimeout(() => {
			this.loadData();
			this.common.stopLoading();
		}, 3000);
	}

	loadData()
	{
		if (this._level == "Admin")
		{
			this.history.getHistoryByLogin()
			.then((res:string) => {
				this._bukti = JSON.parse(res);
			}, (err) => {
				console.log(err);
				this.common.presentAlert('Error','Gagal mengambil data dari server');
			});
		}
		else
		{
			this.history.getByCustomer(this._custId)
				.then((res: string) => { 
					this._bukti = JSON.parse(res);
				}, (err) => {
					console.log(err);
					this.common.presentAlert('Error', 'Gagal mengambil data dari server...');
				});
		}	
		
	}

	detailBukti(id)
	{
		this.navCtrl.push(DetailBuktiPage, {id: id});
	}

}



