import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams, Content } from 'ionic-angular';
import { CommonProvider } from '../../providers/common/common';
import { ConfigProvider } from '../../providers/config/config';
import { hubConnection } from "signalr-no-jquery";
import { LoginProvider } from '../../providers/login/login';


@IonicPage()
@Component({
	selector: 'page-chating',
	templateUrl: 'chating.html',
})
export class ChatingPage {

	@ViewChild(Content) content: Content;
	@ViewChild('chat_input') messageInput: ElementRef;

	//SignalR
	private connection: any;
	private proxy: any;
	//SignalR
	private _connId: string;
	private _email: string;
	private _users: any;
	private _activeRoom: Boolean;
	private msgList: any;
	private _userIdToRoom: string;
	private _nameToRoom: string;
	private editorMsg: string;
	private showEmojiPicker = false;
	private _photo: string;
	private _dateNow: string = new Date().toISOString();
	
	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public common: CommonProvider,
		public config: ConfigProvider,
		public auth: LoginProvider
	) {
		
		var parent = this;
		setTimeout(() => {
			this.connectionChat();
		}, 3000);
		
		this.config.getProfile()
			.then(email => {
				this._email = email;
			});
		
		this.getPhoto();
		
	}

	connectionChat()
	{
		var parent = this;
		this.connection = hubConnection('http://www.mor2plg.id/buset/');
		this.proxy = this.connection.createHubProxy('serverChat');
		
		this.proxy.on('usersOnline', function (users) { 
			parent._users = users;
		});

		this.proxy.on('messageRecieved', function(msgs){
			parent.msgList = msgs;

		});
		
		parent.common.showLoading();
		this.connection.start().done((data: any) => { 
			parent._connId = data.id;
			parent.common.presentToast('Conneced' + data.transport.name + ', ID: ' + data.id);
			parent.common.stopLoading();
			
			var User = {
				UserId: data.id,
				Email: parent._email,
				Name: parent._email.substring(0, parent._email.indexOf('@')),
				GroupName: parent._email,
				Photo: null,
				IsMember: true
			};
			parent.proxy.invoke('JoinUser', User);

		}).fail((err: any) => {
			parent.common.presentToast('Error: ' + err);
			parent.common.stopLoading();
		});

	}

	private getPhoto()
	{
		this.common.showLoading();
		this.common.presentToast('Load data photo...');
		this.auth.getPhotoByEmail(this._email)
			.then((photo:any) => {
				this._photo = photo;
				this.common.stopLoading();
			}, (err) => { 
				this.common.presentToast("gagal menngambil photo profil");
				this.common.stopLoading();
			});
	}

	ionViewDidLoad() {
		
	}

	ionViewDidEnter()
	{
		
	}

	ionViewWillLeave()
	{
		hubConnection.stop();
	}

	toMessage(Name: string, UserId: string)
	{
		this.common.showLoading();
		this._activeRoom = true;
		this._nameToRoom = Name;
		this._userIdToRoom = UserId;
		this.common.stopLoading();
	}

	backtoUsers()
	{
		this.common.showLoading();
		this._activeRoom = false;
		this.common.stopLoading();
	}

	sendMsg()
	{
		if (!this.editorMsg.trim()) return;

		var User = {
			UserId: this._connId,
			Email: this._email,
			Name: this._email.substr(0, this._email.indexOf('@')),
			GroupName: this._email,
			IsMember: true,
			Photo: this._photo
		};
		
		this.proxy.invoke('ServerMessage', User, this._userIdToRoom, this.editorMsg);

		this.editorMsg = '';

		if (!this.showEmojiPicker)
		{
			this.focus();
		}	
	}

	onFocus()
	{
		this.showEmojiPicker = false;
		this.content.resize();
		this.scrollToBottom();
	}

	
	scrollToBottom()
	{
		setTimeout(() => {
			if (this.content.scrollToBottom)
			{
				this.content.scrollToBottom();
			}	
		}, 400);
	}

	switchEmojiPicker()
	{
		this.showEmojiPicker = !this.showEmojiPicker;
		if (!this.showEmojiPicker)
		{
			this.focus();
		}	
		else
		{
			this.setTextareaScroll();
		}	
		this.content.resize();
		this.scrollToBottom();
	}

	private setTextareaScroll()
	{
		const textarea = this.messageInput.nativeElement;
		textarea.scrollTop = textarea.scrollHeight;
	}

	private focus()
	{
		if (this.messageInput && this.messageInput.nativeElement)
		{
			this.messageInput.nativeElement.focus();
		}	
	}

}



